@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
html,body,#root{font-family:'Quicksand', sans-serif;width:100%;height:100%;display:flex;justify-content:center;align-items:center;background:#4da0b0;background:linear-gradient(to right, #d39d38, #4da0b0)}#root{overflow:scroll}.app{width:80%;margin:auto;padding:20px;display:flex;flex-wrap:wrap}.item{width:calc(33% - 2 * 20px);height:200px;border:1px solid #fff;position:relative}.item+.item{margin-left:20px}.item:nth-child(3n+1){margin-left:0}.item:nth-child(n+4){margin-top:20px}.item__desc{position:absolute;top:0;right:0;bottom:0;left:0;background:rgba(0,0,0,0.3);opacity:0;transform:scale(0);transition:0.3s;display:flex;justify-content:center;align-items:center}.item:hover .item__desc{opacity:1;transform:scale(1)}

